@import "../css/variable";
.landing {
    height: -583px;
    background: linear-gradient(to right, pink, white);;
    &__slider {
      padding-top: 30px;
    }
    .slide {
      z-index: -1;
    }
    .swiper-button-next {
      width: 48px;
      height: 48px;
      top: 500px !important;
      right: 17%;
      border-radius: 50%;
      background-color: #ff6606;
    }
    .swiper-button-prev {
      width: 48px;
      height: 48px;
      top: 500px !important;
      left: 76%;
      border-radius: 50%;
      background-color: #ff6606;
    }
    .swiper-button-next::after,
    .swiper-button-prev::after {
      color: white;
      font-size: 15px;
    }
    .swiper-pagination-bullet {
      background-color: white;
      opacity: 1;
      width: 11px;
      height: 11px;
      margin: 0px 12px !important;
    }
    .swiper-pagination-bullet-active {
      background-color: $primary;
      position: relative;
      &::after {
        content: "";
        position: absolute;
        border: 1px solid $primary;
        width: 27px;
        height: 27px;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
      }
    }
  }
  
  .blurred-box {
    position: relative;
    left: 180px;
    width: 473px;
    height: 440px;
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(15px);
    border-radius: 2px;
    margin-top: -583px;
  
    z-index: 100 !important;
    > div {
      padding: 30px 50px;
      height: 100%;
      > h2:nth-child(1) {
        font-size: 50px;
        max-width: 268px;
        line-height: 60px;
      }
      > h2:nth-child(2) {
        color: $primary;
        font-size: 50px;
      }
      > p {
        font-size: 16px;
        font-weight: normal;
        font-family: "Regencie";
        color: gray;
        font-style: normal;
        margin-bottom: 50px;
        line-height: 24px;
      }
      > button {
        background: linear-gradient(180deg, #ff6606 0%, #ff7e2e 100%);
        // padding: 20px 80px;
        border: none;
        width: 352px;
        height: 70px;
        color: white;
        font-size: 20px;
        cursor: pointer;
        border-radius: 10px;
        text-transform: uppercase;
      }
    }
  }
  
