  .container {
    position: relative;
    margin-top: 100px;
    > ::after {
      content: "";
      z-index: -1;
      position: absolute;
      width: 60%;
      height: 583px;
      top: 0px;
      left: 0px;
      background-color: #ffe4d3;
    }
  }
  .blurred-container {
    position: absolute;
    right: 60px;
    bottom: 100px;
  }
  .blurred-box-2 {
    width: 294px;
    height: 148px;
    position: relative;
    border-radius: 2px;
    z-index: 100 !important;
    background: rgba(255, 255, 255, 0.8);
    backdrop-filter: blur(12px);
    > div {
      padding: 28px 24px;
      // padding: 24px 28px;
      > h3 {
        font-size: 22px;
      }
      > p {
        font-size: 18px;
        font-weight: 400;
        font-family: "Regencie";
        color: gray;
        font-style: normal;
      }
      > div {
        display: flex;
        justify-content: space-between;
        margin-top: 10px;
        > h3 {
          font-size: 23px;
        }
      }
    }
  }
